<template>
  <div ref="inputWrapper" class="relative w-full">
    <div
      class="relative flex items-center rounded-08 border border-solid"
      :class="
        errorMessage
          ? 'border-constant-status-error text-constant-status-error'
          : 'border-stroke-bold text-content-on-neutral-secondary focus-within:border-constant-brand-primary focus-within:text-constant-brand-primary'
      "
    >
      <textarea
        v-if="multiline"
        ref="textAreaField"
        :autofocus="autofocus"
        :id="inputId"
        :value="value"
        @input="handleInput"
        :required="isRequired"
        class="floating-form-field block w-full appearance-none bg-transparent px-spacing-150 text-left text-body/m/relaxed/500 text-content-on-neutral-primary antialiased focus:outline-none ipad:px-spacing-200 ipad:text-body/m/relaxed/500"
        :class="verticalPadding"
        @focus="handleFocus"
        @blur="handleBlur"
        :minlength="minLength"
        :maxlength="maxLength"
        :name="inputId"
        :placeholder="placeholder"
        :rows="rows"
      />
      <input
        v-else
        ref="inputField"
        :autofocus="autofocus"
        :id="inputId"
        :value="value"
        @input="handleInput"
        :type="passwordType || inputType"
        :placeholder="inputType === 'array' ? placeholder : ''"
        :autocomplete="autocomplete"
        :required="inputType === 'date' || isRequired"
        class="floating-form-field block w-full appearance-none bg-transparent px-spacing-150 text-left text-body/m/relaxed/500 text-content-on-neutral-primary antialiased focus:outline-none ipad:px-spacing-200 ipad:text-body/m/relaxed/500"
        :class="verticalPadding"
        @focus="handleFocus"
        @blur="handleBlur"
        :min="min"
        :max="max"
        :minlength="minLength"
        :maxlength="maxLength"
        :name="inputId"
        @keydown.down.prevent="navigateSuggestions(1)"
        @keydown.up.prevent="navigateSuggestions(-1)"
        @keypress.enter.prevent="handleEnterKey"
      />
      <label
        :for="inputId"
        class="floating-form-field-label absolute left-spacing-0 top-spacing-0 origin-0 px-spacing-150 text-current duration-300 ipad:px-spacing-200"
        :class="verticalPadding"
      >
        {{ label }}
      </label>

      <!-- Date-specific icon handling -->
      <div
        v-if="inputType === 'date'"
        class="absolute bottom-spacing-150 right-spacing-200 top-spacing-150 flex items-center justify-end"
      >
        <IconCalendar
          class="h-spacing-200 w-spacing-200 text-content-on-neutral-tertiary hover:text-content-on-neutral-secondary"
        />
      </div>
      <div
        v-if="inputType === 'password'"
        class="flex w-spacing-400 items-center justify-start"
      >
        <IconEye
          v-show="passwordType === 'password'"
          @click.native="changePasswordType('text')"
          class="h-spacing-200 w-spacing-200 cursor-pointer text-content-on-neutral-tertiary hover:text-content-on-neutral-secondary"
        />
        <IconEyeOff
          v-show="passwordType === 'text'"
          @click.native="changePasswordType('password')"
          class="h-spacing-200 w-spacing-200 cursor-pointer text-content-on-neutral-tertiary hover:text-content-on-neutral-secondary"
        />
      </div>
      <div
        v-if="checkAvailability && value"
        class="flex w-spacing-400 items-center justify-start"
      >
        <IconLoader2
          class="h-spacing-200 w-spacing-200 animate-spin text-constant-brand-primary"
          v-if="checkingAvailability"
        />
        <IconCheck
          v-else-if="isAvailable"
          class="h-spacing-200 w-spacing-200 rounded-full border border-constant-status-success p-spacing-25 text-constant-status-success"
        />
        <IconAlertTriangle
          v-else
          class="h-spacing-200 w-spacing-200 text-constant-status-error"
        />
      </div>
      <div
        v-if="inputType === 'array'"
        class="absolute bottom-spacing-200 right-spacing-200 top-spacing-200 flex items-center justify-end"
      >
        <div
          @click="handleAddToArray()"
          class="flex h-spacing-300 w-spacing-300 cursor-pointer items-center justify-center rounded-full bg-constant-brand-primary hover:bg-hover-constant-brand-primary"
        >
          <IconPlus
            class="h-spacing-250 w-spacing-250 text-content-on-neutral-primary"
          />
        </div>
      </div>
      <div
        v-if="clearButton && value !== ''"
        @click="handleClearButtonAction"
        class="absolute bottom-spacing-200 right-spacing-200 top-spacing-200 flex cursor-pointer items-center justify-end"
      >
        <IconX
          class="h-spacing-250 w-spacing-250 text-content-on-neutral-tertiary hover:text-content-on-neutral-secondary"
        />
      </div>
    </div>

    <!-- Generalized suggestion dropdown -->
    <div
      v-if="
        (inputType === 'searchable-input' && suggestions?.length) ||
        isSuggestionsFetching
      "
      class="absolute left-spacing-0 right-spacing-0 z-[101] mt-spacing-25 max-h-[250px] w-full overflow-y-auto rounded-08 bg-bg-neutral-default shadow-card"
    >
      <template v-if="isSuggestionsFetching">
        <div
          v-for="(_, index) in new Array(4).fill('')"
          :key="index"
          class="border-b border-stroke-bold bg-bg-neutral-raised px-spacing-150 py-spacing-150"
        >
          <GeneralSkeleton
            class="h-spacing-250 w-full"
            skeletonBackgroundColor="bg-bg-neutral-disabled"
          />
        </div>
      </template>
      <template v-else>
        <div
          v-for="(suggestion, index) in suggestions"
          :key="index"
          ref="suggestionRefs"
          @mousedown.prevent="selectSuggestion(suggestion)"
          :class="
            index === highlightedIndex
              ? 'bg-bg-neutral-disabled'
              : 'bg-bg-neutral-raised'
          "
          class="cursor-pointer border-b border-stroke-bold px-spacing-150 py-spacing-150 hover:bg-bg-neutral-disabled"
        >
          <div v-if="suggestion?.tree_view">
            <div
              v-for="(item, index) in suggestion?.path"
              :key="index"
              class="flex flex-col gap-spacing-50"
            >
              <p
                class="relative mt-spacing-25 text-body/m/relaxed/400"
                :class="
                  index === suggestion?.path?.length - 1
                    ? 'text-content-on-neutral-primary'
                    : 'text-content-on-neutral-secondary'
                "
              >
                <span class="opacity-0">{{ "-----".repeat(index) }}</span
                ><span
                  class="relative -top-[3px] inline-block h-spacing-250 w-spacing-250 min-w-spacing-250 rounded-bl-08 border-b border-l border-content-on-neutral-secondary"
                ></span
                ><span
                  v-if="index === suggestion?.path?.length - 1"
                  v-html="
                    $highlightMatch(
                      suggestion?.path[suggestion?.path?.length - 1],
                      value,
                    )
                  "
                />
                <span v-else>{{ item }}</span>
              </p>
            </div>
          </div>
          <p
            v-else-if="suggestion?.is_html && suggestion?.htmlContent"
            class="text-body/m/relaxed/400 text-content-on-neutral-primary"
            v-html="suggestion?.htmlContent"
          />
          <p
            v-else-if="!suggestion?.is_html"
            class="text-body/m/relaxed/400 text-content-on-neutral-primary"
          >
            {{ suggestion?.display_name }}
          </p>
        </div>
      </template>
    </div>

    <p
      v-if="showError"
      class="mt-spacing-50 min-h-spacing-200 text-left text-body/s/relaxed/400 text-constant-status-error"
    >
      {{ errorMessage ? errorMessage : "" }}
    </p>
  </div>
</template>

<script>
/**
 * @emits input - Emitted when the input value changes.
 * @param {String|Number} value - The current input value.
 *
 * @emits fetch-suggestions - Emitted when input type is "searchable-input" and user types.
 * @param {String} query - The current input text to fetch suggestions.
 *
 * @emits set-suggestion - Emitted when a suggestion is selected from the dropdown.
 * @param {Object} suggestion - The selected suggestion object.
 *
 * @emits reset-suggestions - Emitted when suggestions need to be cleared.
 *
 * @emits enter-pressed - Emitted when the Enter key is pressed to finalize the input.
 */
export default {
  props: {
    label: {
      type: String,
      required: true,
    },
    value: {
      type: [String, Number],
      required: false,
    },
    inputId: {
      type: String,
      default: "input-field",
    },
    inputType: {
      type: String,
      default: "text",
    },
    placeholder: {
      type: String,
      required: false,
    },
    autocomplete: {
      type: String,
      default: "off",
    },
    isRequired: {
      type: Boolean,
      default: false,
    },
    autofocus: {
      type: Boolean,
      required: false,
      default: false,
    },
    min: {
      type: Number,
      required: false,
    },
    max: {
      type: Number,
      required: false,
    },
    minLength: {
      type: Number,
      required: false,
    },
    maxLength: {
      type: Number,
      required: false,
    },
    multiline: {
      type: Boolean,
      required: false,
      default: false,
    },
    rows: {
      type: Number,
      default: 5,
      required: false,
    },
    suggestions: {
      type: Array,
      default: () => [],
    },
    isSuggestionsFetching: {
      type: Boolean,
      required: false,
      default: false,
    },
    preventVModel: {
      type: Boolean,
      required: false,
      default: false,
    },
    clearButton: {
      type: Boolean,
      required: false,
      default: false,
    },
    verticalPadding: {
      type: String,
      default: "py-spacing-150",
      required: false,
    },
    showError: {
      type: Boolean,
      default: true,
      required: false,
    },
    fieldValidations: {
      type: Object,
      default: null,
      required: false,
    },
    trim: {
      type: Boolean,
      default: true,
    },
    order: {
      type: Number,
      default: 0,
    },
    checkAvailability: {
      type: Boolean,
      required: false,
      default: false,
    },
    isAvailable: {
      type: Boolean,
      required: false,
      default: true,
    },
    notAvailableErrorMessage: {
      type: String,
      default: "",
      required: false,
    },
    checkingAvailability: {
      type: Boolean,
      default: false,
      required: false,
    },
  },
  components: {
    IconCalendar: () => import("@/components/svgIcons/calendar.vue"),
    IconX: () => import("@/components/svgIcons/x.vue"),
    IconAlertTriangle: () => import("@/components/svgIcons/alert-triangle.vue"),
    IconPlus: () => import("@/components/svgIcons/plus.vue"),
    IconEye: () => import("@/components/svgIcons/eye.vue"),
    IconCheck: () => import("@/components/svgIcons/check.vue"),
    IconEyeOff: () => import("@/components/svgIcons/eye-off.vue"),
    GeneralSkeleton: () => import("@/components/General/Skeleton.vue"),
    IconLoader2: () => import("@/components/svgIcons/loader-2.vue"),
  },
  data() {
    return {
      highlightedIndex: -1,
      errorMessage: "",
      passwordType: this.inputType,
    };
  },
  watch: {
    isAvailable: {
      handler(newValue) {
        if (this.checkAvailability) {
          if (!newValue && this.notAvailableErrorMessage) {
            this.errorMessage = this.notAvailableErrorMessage;
          } else {
            this.errorMessage = "";
          }
        }
      },
      immediate: true,
    },
  },
  methods: {
    handleInput(event) {
      const value = event?.target?.value;

      // Handle phone number formatting
      if (this.inputType === "phoneNumber") {
        const formattedValue = this.formatPhoneNumber(value, event.target);
        this.$emit("input", formattedValue);
        return;
      }

      this.$emit("input", value);
      if (this.inputType === "searchable-input") {
        this.$emit("fetch-suggestions", value);
        this.highlightedIndex = -1;
      }
    },
    formatPhoneNumber(value, inputElement) {
      // Save cursor position
      let cursorPosition = inputElement?.selectionStart;

      // Ensure we only keep digits (but allow intentional zeroes)
      const digits = value?.replace(/\D/g, "");

      // Phone number format
      let formatted = "+0000 000 00 00 000".split("");
      let digitIndex = 0;

      for (let i = 0; i < formatted?.length; i++) {
        if (formatted[i] === "0" && digitIndex < digits?.length) {
          formatted[i] = digits[digitIndex++];
        }
      }

      const newValue = formatted.join("");

      // Prevent cursor from going before the "+" sign
      cursorPosition = Math.max(cursorPosition, 1);

      // Restore cursor position, adjusted for added formatting
      const offset = this.calculateCursorOffset(
        value,
        newValue,
        cursorPosition,
      );
      inputElement.value = newValue;
      inputElement.setSelectionRange(
        cursorPosition + offset,
        cursorPosition + offset,
      );

      return newValue;
    },

    calculateCursorOffset(oldValue, newValue, cursorPosition) {
      // Calculate the offset based on the difference between old and new values
      let offset = 0;

      for (let i = 0; i < cursorPosition; i++) {
        if (oldValue[i] !== newValue[i]) {
          offset++;
        }
      }

      return offset;
    },

    changePasswordType(type) {
      this.passwordType = type;
    },
    selectSuggestion(suggestion) {
      if (this.isSuggestionsFetching) {
        return;
      }
      if (!this.preventVModel) {
        this.$emit(
          "input",
          suggestion?.originalTitle || suggestion?.display_name,
        );
      }
      this.$emit("set-suggestion", suggestion);
      this.$emit("reset-suggestions");
      this.highlightedIndex = -1;
      this.$emit("enter-pressed");
    },
    navigateSuggestions(direction) {
      if (
        this.inputType !== "searchable-input" ||
        this.suggestions?.length === 0
      )
        return;
      this.highlightedIndex =
        (this.highlightedIndex + direction + this.suggestions.length) %
        this.suggestions.length;

      // Scroll the highlighted suggestion into view
      this.$nextTick(() => {
        const suggestionElement =
          this.$refs.suggestionRefs?.[this.highlightedIndex];
        if (suggestionElement) {
          suggestionElement.scrollIntoView({
            behavior: "smooth",
            block: "nearest",
          });
        }
      });
    },
    selectHighlightedSuggestion() {
      if (this.highlightedIndex >= 0) {
        this.selectSuggestion(this.suggestions[this.highlightedIndex]);
      }
    },
    handleBlur() {
      if (this.trim && this.inputType !== "number") {
        if (this.value) this.$emit("input", this.value?.trim());
      }

      if (this.inputType === "phoneNumber" && this.value.length < 19) {
        this.$emit("input", "+0000 000 00 00 000");
      }

      this.validate(true);
      this.$emit("blur");
      if (this.inputType === "searchable-input") {
        this.$emit("reset-suggestions", true);
        this.highlightedIndex = -1;
      }
      return true;
    },
    handleFocus() {
      if (this.inputType === "phoneNumber") {
        if (this.value.length < 19) {
          this.$emit("input", "+0000 000 00 00 000");
        }
        setTimeout(() => {
          if (this.value === "+0000 000 00 00 000") {
            this.$refs.inputField.setSelectionRange(1, 1);
          }
        }, 0);
      }
      this.$emit("focus");
      this.errorMessage = "";
      return true;
    },
    handleEnterKey() {
      if (this.inputType === "searchable-input") {
        this.selectHighlightedSuggestion();
      }
      this.$emit("enter-pressed");
    },
    handleClearButtonAction() {
      this.$emit("input", "");
      this.$emit("clear-button-press");
      if (this.inputType === "searchable-input") {
        this.$emit("clear-suggestions", "");
        this.highlightedIndex = -1;
      }
    },
    handleAddToArray() {
      this.$emit("add-item-to-array");
    },
    validate(scroll) {
      if (!this.fieldValidations) {
        return true;
      }
      let error = this.fieldValidations?.validate(this.value);
      if (this.checkAvailability && !this.isAvailable) {
        error = this.notAvailableErrorMessage;
      }
      if (error) {
        this.errorMessage = error;
        if (scroll) {
          this.scrollToComponent();
        }
        return false;
      }
      return true;
    },
    scrollToComponent() {
      if (this.multiline) {
        this.$refs.textAreaField?.scrollIntoView({
          behavior: "smooth",
          block: "center",
        });
      } else {
        this.$refs.inputField?.scrollIntoView({
          behavior: "smooth",
          block: "center",
        });
      }
    },
  },
};
</script>

<style scoped>
input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-calendar-picker-indicator {
  z-index: 100;
  opacity: 0;
  position: absolute;
  right: 10px;
  width: 30px;
  cursor: pointer;
  height: 40px;
}
</style>
